<template>
  <div class="course-question">
    <head-top title="学生提问"></head-top>

    <van-search v-model="keyWord" show-action placeholder="请输入搜索关键词" @search="onSearch">
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>

    <!-- 聊天信息 -->
    <div v-if="questionList && questionList.length>0" class="chat" ref="chat">
      <div class="chat_content">
        <div v-for="(sub, k) in questionList" :key="k" class="chat_item left_item">
          <div class="chat_icon">
            <img :src="defaultPhotoImg" alt=""/>
          </div>

          <div class="chat_news">
            <div class="chat_news_name">
              {{sub['studentName']}}
              <span class="time">{{sub.createTime}}</span>
            </div>
            <div class="chat_news_cont">
              <span>{{sub.content}}</span>
              <div class="chat_news_arrow"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-cell v-else title="暂无数据"></van-cell>

  </div>
</template>

<script>
import defaultImg from "../../../../assets/boy_img.png";
import { formatDate } from "@/utils/formatData";

export default {
  name: "question",
  data () {
    return {
      defaultPhotoImg: defaultImg,
      studentList: [],
      questionList: [],
      keyWord: "",
      isForbid: true
    };
  },
  mounted () {
    this.getQuestionList();
  },
  methods: {
    getQuestionList () {
      this.questionList = [];
      this.$gatewayHttp.question.getDataList({
        key: this.keyWord
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
            });
            res.Datas.forEach(item => {
              this.questionList.push({
                studentName: item["StudentName"],
                studentID: item["StudentID"],
                content: item["QContent"],
                id: item["QID"],
                type: item["QType"],
                createTime: formatDate(item["CreateTime"], "hh:mm:ss")
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    onSearch () {
      this.getQuestionList();
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-question {
  min-height: 100%;
  /*background-color: #fff;*/

  .chat {
    background-color: #efefef;

    .chat_content {
      .chat_news_name {
        .time {
          color: #888;
        }
      }
    }
  }
}
</style>
